// import { Component } from '../react'
import { setAttribute } from './dom';
import { _render } from './render'


/**
 * @param {HTMLElement} dom ÁúüÂÆûDOM
 * @param {vnode} vnode ËôöÊãüDOM
 * @param {HTMLElement} container ÂÆπÂô®
 * @returns {HTMLElement} Êõ¥Êñ∞ÂêéÁöÑDOM
**/
/* 
    diffÁöÑÂàùË°∑ÔºöÊØèÊ¨°Êõ¥Êñ∞ÈÉΩÈáçÊñ∞Ê∏≤ÊüìÊï¥‰∏™Â∫îÁî®ÊàñËÄÖÊï¥‰∏™ÁªÑ‰ª∂ÔºåDOMÊìç‰ΩúÊ∂àËÄóÊÄßËÉΩÂæà‰∏•Èáç
    ‰∏∫‰∫ÜÂáèÂ∞ëDOMÊõ¥Êñ∞Â∏¶Êù•ÁöÑÊÄßËÉΩÊ∂àËÄóÔºåÊâæÂà∞Ê∏≤ÊüìÂâçÂêéÁúüÊ≠£ÂèòÂåñÁöÑÈÉ®ÂàÜÔºåÂè™Êõ¥Êñ∞Ëøô‰∏ÄÈÉ®ÂàÜDOM
    ÂØπÊØîËôöÊãüDOMÂíåÁúüÊòØDOMÊúÄÂêéËøîÂõûÊõ¥Êñ∞ÂêéÁöÑDOM
*/
export function diff(dom, vnode, container) {
    const result = diffNode( dom, vnode );

    if ( container && result.parentNode !== container ) {
        container.appendChild( result );
    }

    return result;
}
/* 
    diffNode
    ÂØπÊØîËäÇÁÇπËá™Ë∫´
*/
function diffNode(dom, vnode) {
    // ‰ªé‰∏äÂà∞‰∏ãÔºå‰ªéÂ∑¶ÂæÄÂè≥ÈÄê‰∏™Â§ÑÁêÜÊØè‰∏™domËäÇÁÇπÔºàÂ¶ÇÊûúÊòØ‰øÆÊîπÁöÑËØùÔºâÂíåvnode
    let out = dom;

    // undefined/null/booleanÁöÑÂ§ÑÁêÜ
    if ( vnode === undefined || vnode === null || typeof vnode === 'boolean' ) {
        vnode = '';
    }

    // ÂØπÊØîÊï∞Â≠ó
    if ( typeof vnode === 'number' ) {
        vnode = String( vnode );
    }

    // ÂØπÊØîÂ≠óÁ¨¶‰∏≤
    if (typeof vnode === "string") {
        // ÂΩìÂâçDOMËäÇÁÇπÊòØÊñáÊú¨ËäÇÁÇπÔºåÁõ¥Êé•Êõ¥Êñ∞ÂÜÖÂÆπ
        // nodeType: https://developer.mozilla.org/zh-CN/docs/Web/API/Node/nodeType
        if (dom && dom.nodeType === 3) {
            if (dom.textContent !== vnode) {
                dom.textContent = vnode;
            }
        // Â¶ÇÊûúDOM‰∏çÊòØÊñáÊú¨ËäÇÁÇπÔºåÂàôÊñ∞Âª∫‰∏Ä‰∏™ÊñáÊú¨ËäÇÁÇπDOMÔºåÂπ∂ÁßªÈô§ÊéâÂéüÊù•ÁöÑ„ÄÇÊñ∞Âª∫ÁöÑÊñáÊú¨ËäÇÁÇπÊääÂéüÊù•ÁöÑËäÇÁÇπÁöÑ‰ΩçÁΩÆÂç†Áî®‰∫Ü
        } else {
            out = document.createTextNode(vnode);
            if (dom && dom.parentNode) {
                dom.parentNode.replaceChild(out, dom);
            }
        }
        // ÊñáÊú¨ËäÇÁÇπÈùûÂ∏∏ÁÆÄÂçïÊ≤°ÊúâÂ±ûÊÄßÔºåÊ≤°ÊúâÂ≠êÂÖÉÁ¥†
        return out;
    }

    // ÂØπÊØîÁªÑ‰ª∂
    if (typeof vnode.tag === 'function') {
        return diffComponent(dom, vnode);
    }
    // ÊÉÖÂÜµ‰∏ÄÔºöÂ¶ÇÊûúÁúüÂÆûDOM‰∏çÂ≠òÂú®ÔºåË°®Á§∫Ê≠§ËäÇÁÇπÊòØÊñ∞Â¢ûÁöÑÔºåÊàñËÄÖÊñ∞Êóß‰∏§‰∏™ËäÇÁÇπÁöÑÁ±ªÂûã‰∏ç‰∏ÄÊ†∑Ôºå
    // ÈÇ£‰πàÂ∞±Êñ∞Âª∫‰∏Ä‰∏™DOMÂÖÉÁ¥†ÔºåÂπ∂Â∞ÜÂéüÊù•ÁöÑÂ≠êËäÇÁÇπÔºàÂ¶ÇÊûúÊúâÁöÑËØùÔºâÁßªÂä®Âà∞Êñ∞Âª∫ÁöÑDOMËäÇÁÇπ‰∏ã„ÄÇ
    if (!dom || !isSameNodeType( dom, vnode )) {
        out = document.createElement(vnode.tag);
        
        // ‰∏çÊòØÂêå‰∏ÄÁ±ªÂûãÁöÑdom
        if (dom) {
            // Â∞ÜÂéüÊù•ÁöÑÂ≠êËäÇÁÇπÁßªÂà∞Êñ∞ËäÇÁÇπ‰∏ã
            [...dom.childNodes].map(out.appendChild);
            if (dom.parentNode) {
                // ÁßªÈô§ÊéâÂéüÊù•ÁöÑDOMÂØπË±°
                dom.parentNode.replaceChild(out, dom);
            }
        }
    }


    // ÊÉÖÂÜµ‰∫åÔºöÂ¶ÇÊûúÁúüÂÆûDOMÂ≠òÂú®ÔºåÂπ∂‰∏îÂíåËôöÊãüDOMÊòØÂêå‰∏ÄÁ±ªÂûãÁöÑÔºåÈÇ£Êàë‰ª¨ÊöÇÊó∂‰∏çÈúÄË¶ÅÂÅöÂà´ÁöÑÔºå
    // Âè™ÈúÄË¶ÅÁ≠âÂæÖÂêéÈù¢ÂØπÊØîÂ±ûÊÄßÂíåÂØπÊØîÂ≠êËäÇÁÇπ„ÄÇ
    if ( vnode.children && vnode.children.length > 0 || ( out.childNodes && out.childNodes.length > 0 ) ) {
        diffChildren( out, vnode.children );
    }

    // ÂØπÊØîÂ±ûÊÄß
    diffAttributes( out, vnode );

    return out;
}
/* 
    diffChildren
    ÂØπÊØîÂ≠êËäÇÁÇπ
*/
function diffChildren(dom, vchildren) {
    // ÂØπÊØîÂ≠êËäÇÁÇπÊó∂ÔºåÂ≠êËäÇÁÇπÊòØ‰∏Ä‰∏™Êï∞ÁªÑÔºåÂÆÉ‰ª¨ÂèØËÉΩÊîπÂèò‰∫ÜÈ°∫Â∫èÔºåÊï∞Èáè„ÄÇÂæàÈöæÁ°ÆÂÆöË¶ÅÂíåËôöÊãüDOMÂØπÊØîÁöÑÊòØÂì™‰∏Ä‰∏™
    // ËøôÈáåÂ∞±Ë¶Å‰∏∫ÊØè‰∏Ä‰∏™Â≠êËäÇÁÇπËÆæ‰∏Ä‰∏™keyÔºåÈáçÊñ∞Ê∏≤ÊüìÊó∂ÂØπÊØîkeyÂÄºÁõ∏ÂêåÁöÑËäÇÁÇπ
    const domChildren = dom.childNodes;
    const children = [];

    const keyed = {};

    // ÂàíÂàÜËäÇÁÇπÔºöÂ∞ÜÊúâkeyÁöÑËäÇÁÇπÂíåÊ≤°ÊúâkeyÁöÑËäÇÁÇπÂàÜÂºÄ
    if (domChildren.length > 0) {
        for (let index = 0; index < domChildren.length; index++) {
            const child = domChildren[index];
            const key = child.key;
            if (key) {
                // ÊúâkeyÁöÑ
                keyed[key] = child;
            } else {
                // Ê≤°ÊúâkeyÁöÑ
                children.push(child);
            }
        }
    }

    // ËôöÊãüDOMÁöÑchildren
    if (vchildren && vchildren.length > 0) {
        let min = 0;
        let childrenLen = children.length;

        for (let index = 0; index < vchildren.length; index++) {
            const vchild = vchildren[index];
            const key = vchild.key;
            let child;

            // Â¶ÇÊûúÊúâkeyÁöÑËØùÔºåÂú®ÂΩìÂâçdomÁöÑÂ≠êËäÇÁÇπÊúâkeyÁöÑÂØπË±°‰∏≠‰πüÊâæÂæóÂà∞ÂàôÊõ¥Êñ∞Â≠êËäÇÁÇπÁöÑÂÄº
            if (key) {
                if (keyed[key]) {
                    child = keyed[key];
                    keyed[key] = undefined;
                }
            } else if (min < childrenLen) { // Â≠êËäÇÁÇπÂ≠òÂú®ÁöÑËØù
                for (let index = min; index < childrenLen; index++) { // ËôöÊãüËäÇÁÇπÊ≤°ÊúâkeyÁöÑËäÇÁÇπÊï∞Â∞è‰∫éÂΩìÂâçdomÊ≤°ÊúâkeyÁöÑËäÇÁÇπÊï∞
                    let c = children[index]; // ÂΩìÂâçÊ≤°ÊúâkeyÁöÑdom
                    if (c && isSameNodeType( c, vchild )) { // ÂΩìÂâçÊ≤°ÊúâkeyÁöÑdomÊòØÂê¶Â≠òÂú®ÔºåÂèØËÉΩË¢´Âà†Èô§‰∫Ü
                        child = c;
                        children[index] = undefined;
                        if (index === childrenLen -1 ) {
                            childrenLen--;
                        }
                        if (index === min) {
                            min++;
                        }
                        break;
                    }
                }
            }

            // ÂØπÊØî
            child = diff(child, vchild);

            // Êõ¥Êñ∞DOM
            const f = domChildren[index];
            if (child && child !== dom && child !== f) {
                // Â¶ÇÊûúÊõ¥Êñ∞ÂâçÁöÑÂØπÂ∫î‰ΩçÁΩÆ‰∏∫Á©∫ÔºåËØ¥ÊòéÊ≠§ËäÇÁÇπÊó∂Êñ∞Â¢ûÁöÑ
                if (!f) {
                    dom.appendChild(child);
                // Â¶ÇÊûúÊõ¥Êñ∞ÂêéÁöÑËäÇÁÇπÂíåÊõ¥Êñ∞ÂâçÂØπÂ∫î‰ΩçÁΩÆÁöÑ‰∏ã‰∏Ä‰∏™ËäÇÁÇπ‰∏ÄÊ†∑ÔºåËØ¥ÊòéÂΩìÂâç‰ΩçÁΩÆÁöÑËäÇÁÇπË¢´ÁßªÈô§‰∫Ü
                } else if (child === f.nextSibling) {
                    removeNode(f);
                // Â∞ÜÊõ¥Êñ∞ÂêéÁöÑËäÇÁÇπÁßªÂä®Âà∞Ê≠£Á°ÆÁöÑ‰ΩçÁΩÆ
                } else {
                    // Ê≥®ÊÑèinsertBeforeÁöÑÁî®Ê≥ïÔºåÁ¨¨‰∏Ä‰∏™ÂèÇÊï∞ÊòØË¶ÅÊèíÂÖ•ÁöÑËäÇÁÇπÔºåÁ¨¨‰∫å‰∏™ÂèÇÊï∞ÊòØÂ∑≤Â≠òÂú®ÁöÑËäÇÁÇπ
                    dom.insertBefore(child, f);
                }
            }
        }
    }
}
/* 
    diffComponent
    ÂØπÊØîÁªÑ‰ª∂
*/
function diffComponent(dom, vnode) {
    let c = dom && dom._component;
    let oldDom = dom;

    // Â¶ÇÊûúÁªÑ‰ª∂Á±ªÂûãÊ≤°ÊúâÂèòÂåñÂàôÈáçÊñ∞set props
    if (c && c.constructor === vnode.tag) {
        setComponentProps(c, vnode.attrs);
        dom = c.base;
        // Â¶ÇÊûúÁªÑ‰ª∂Á±ªÂûãÂèòÂåñÔºåÂàôÁßªÈô§ÊéâÂéüÊù•ÁªÑ‰ª∂ÔºåÂπ∂Ê∏≤ÊüìÊñ∞ÁöÑÁªÑ‰ª∂
    } else {
        if (c) {
            unmountComponent(c);
            oldDom = null;
        }

        c = createComponent(vnode.tag, vnode.attrs);

        setComponentProps(c, vnode.attrs);
        dom = c.base;

        if (oldDom && dom !== oldDom) {
            oldDom._component = null;
            removeNode(oldDom);
        }
    }
    return dom;
}
/* 
    diffAttributes
    ÂØπÊØîËäÇÁÇπÂ±ûÊÄß
*/
function diffAttributes( dom, vnode ) {
    // diffÁÆóÊ≥ï‰∏ç‰ªÖË¶ÅÊâæÂá∫ËäÇÁÇπÁ±ªÂûãÁöÑÂèòÂåñÔºåËøòË¶ÅÊâæÂá∫ËäÇÁÇπÁöÑÂ±ûÊÄß‰ª•Âèä‰∫ã‰ª∂ÁõëÂê¨ÁöÑÂèòÂåñ
    const old = {}; // ÂΩìÂâçDOMÁöÑÂ±ûÊÄß
    const attrs = vnode.attrs; // ËôöÊãüDOMÁöÑÂ±ûÊÄß
    // Ëé∑ÂèñÂΩìÂâçDOMËäÇÁÇπÁöÑÂ±ûÊÄß
    for (let index = 0; index < dom.attributes.length; index++) {
        const attr = dom.attributes[index];
        old[attr.name] = attr.value;
    }

    // Â¶ÇÊûúÂéüÊù•ÁöÑÂ±ûÊÄß‰∏çÂú®Êñ∞Â±ûÊÄßÂΩì‰∏≠ÔºåÂàôÂ∞ÜÂÖ∂ÁßªÈô§Êéâ
    for (let name in old) {
        if (!(name in attrs)) {
            setAttribute(dom, name, undefined);
        }
    }

    // Êõ¥Êñ∞ÂΩìÂâçdomÁöÑÂ±ûÊÄßÂÄº
    for (let name in attrs) {
        if (old[name] !== attrs[name]) {
            setAttribute(dom, name, attrs[name]);
        }
    }
}
/* 
    isSameNodeType
    ÊòØÂê¶ÊòØÂêå‰∏ÄÁ±ªÂûãÁöÑnode
*/
function isSameNodeType( dom, vnode ) {
    if ( typeof vnode === 'string' || typeof vnode === 'number' ) {
        return dom.nodeType === 3;
    }

    if ( typeof vnode.tag === 'string' ) {
        return dom.nodeName.toLowerCase() === vnode.tag.toLowerCase();
    }

    return dom && dom._component && dom._component.constructor === vnode.tag;
}
/* 
    renderComponent
    Ê∏≤ÊüìÁªÑ‰ª∂
*/
export function renderComponent(component) {
    let base;
    // Ë∞ÉÁî®ÁªÑ‰ª∂ÁöÑrenderÂáΩÊï∞ËøîÂõûdomÂØπË±°
    const renderer = component.render();
    if (component.base && component.componentWillUpdate) {
        component.componentWillUpdate();
    }
    // base = _render(renderer);
    base = diff(component.base, renderer);
    

    if ( component.base ) {
        if ( component.componentDidUpdate ) component.componentDidUpdate();
    } else if ( component.componentDidMount ) {
        component.componentDidMount();
    }

    // if ( component.base && component.base.parentNode ) {
    //     component.base.parentNode.replaceChild( base, component.base );
    // }

    // component.base‰øùÂ≠òÁªÑ‰ª∂ÂÆû‰æãÊúÄÁªàÊ∏≤ÊüìÂá∫Êù•ÁöÑDOM
    // ÂèçËøáÊù•base._component‰øùÂ≠òÁöÑÊòØdomÂØπË±°ÊâÄÂØπÂ∫îÁöÑÁªÑ‰ª∂ÔºåËøô‰∏™Â∞±ÊòØ‰∏∫‰∫ÜÊää‰ªñ‰ª¨ÂÖ≥ËÅîËµ∑Êù•
    component.base = base;
    base._component = component;
}
/* 
    setComponentProps
    ËÆæÁΩÆÁªÑ‰ª∂Â±ûÊÄß
*/
export function setComponentProps(component, props) {
    if (!component.base) {
        if (component.componentWillMount) {
            component.componentWillMount();
        } else if (component.compoentWillReceiveProps) {
            component.compoentWillReceiveProps();
        }

        component.props = props;
        // renderComponentÊñπÊ≥ïÁî®Êù•Ê∏≤ÊüìÁªÑ‰ª∂ÔºåsetStateÊñπÊ≥ï‰ºöÁõ¥Êé•Ë∞ÉÁî®Ëøô‰∏™ÊñπÊ≥ïËøõË°åÈáçÊñ∞Ê∏≤Êüì
        // Âú®Ëøô‰∏™ÊñπÊ≥ïÈáåÂèØ‰ª•ÂÆûÁé∞componentWillUpdate, componentDidUpdate, componentDidMount
        renderComponent(component);
    }
}
/* 
    createComponent
    ÂàõÂª∫ÁªÑ‰ª∂ÂÆû‰æã
*/
export function createComponent(Constructor, props) {
    // Constructor === component
    let inst;
    // Â¶ÇÊûúÊòØÁ±ªÂÆö‰πâÁªÑ‰ª∂ÔºåÂàôÁõ¥Êé•ËøîÂõûÂÆû‰æã
    if (Constructor.prototype && Constructor.prototype.render) {
        inst = new Constructor(props);
        // Â¶ÇÊûúÊòØÂáΩÊï∞ÂÆö‰πâÁªÑ‰ª∂ÔºåÂàôÂ∞ÜÂÖ∂Êâ©Â±ï‰∏∫Á±ªÂÆö‰πâÁªÑ‰ª∂
    } else {
        inst = new Constructor(props);
        inst.constructor = Constructor;
        inst.render = function () {
            return this.constructor(props);
        }
    }
    return inst;
}
/* 
    unmountComponent
    Âç∏ËΩΩÁªÑ‰ª∂
*/
function unmountComponent( component ) {
    if ( component.componentWillUnmount ) component.componentWillUnmount();
    removeNode( component.base);
}