import { createComponent, setComponentProps } from './diff';
import { setAttribute } from './dom';
/* 
    Ê∏≤ÊüìÂáΩÊï∞
    _render
*/
export function _render(vnode) {
    if (vnode === undefined || vnode === null || typeof vnode === 'boolean') { 
        vnode = '';
    }
    if (typeof vnode === 'number') {
        vnode = String(vnode);
    }

    // function Welcome( props ) {
    //     return <h1>Hello, {props.name}</h1>;
    // }
    // tag ÊòØ Welcome
    // Âå∫ÂàÜÁªÑ‰ª∂ÂíåÂéüÁîüDOMÁöÑÂ∑•‰ΩúÔºåÊòØbabel-plugin-transform-react-jsxÂ∏ÆÊàë‰ª¨ÂÅöÁöÑ
    if (typeof vnode.tag === 'function') {
        // createComponentÊñπÊ≥ïÂàõÂª∫ÁªÑ‰ª∂ÂÆû‰æãÔºåÂπ∂‰∏îÂ∞ÜÂáΩÊï∞ÂÆö‰πâÁªÑ‰ª∂Êâ©Â±ï‰∏∫Á±ªÂÆö‰πâÁªÑ‰ª∂ËøõË°åÂ§ÑÁêÜ
        const component = createComponent(vnode.tag, vnode.attrs);
        setComponentProps(component, vnode.attrs);
        return component.base;
    }
    // ÂΩìvnode‰∏∫Â≠óÁ¨¶‰∏≤Êó∂ÔºåÊ∏≤ÊüìÁªìÊûúÊòØ‰∏ÄÊÆµÊñáÊú¨
    if (typeof vnode === 'string' || typeof vnode === 'number') {
        let textNode = document.createTextNode(vnode)
        return textNode;
    }

    const dom = document.createElement(vnode.tag);

    if (vnode.attrs) {
        Object.keys(vnode.attrs).forEach(key => {
            const value = vnode.attrs[key];
            setAttribute(dom, key, value); // ËÆæÁΩÆÂ±ûÊÄß
        });
    }
    vnode.children.forEach(child => render(child, dom)); // ÈÄíÂΩíÊ∏≤ÊüìÂ≠êËäÇÁÇπ

    return dom; 
}
/* 
    renderÁöÑÁ¨¨‰∏Ä‰∏™ÂèÇÊï∞Êé•Êî∂ÁöÑÊòØcreateElementËøîÂõûÁöÑÂØπË±°
    Á¨¨‰∫å‰∏™ÂèÇÊï∞ÊòØÊåÇËΩΩÁöÑÁõÆÊ†áDOM
    renderÁöÑ‰ΩúÁî®Â∞±ÊòØÂ∞ÜËôöÊãüDOMÊ∏≤ÊüìÊàêÁúüÂÆûÁöÑDOM
*/
function render (vnode, container) {
    container.innnerHTML = '';
    // Â∞ÜÊ∏≤ÊüìÁªìÊûúÊåÇËΩΩÂà∞ÁúüÊ≠£ÁöÑDOM‰∏ä
    return container.appendChild(_render(vnode));
}

export default render