import { renderComponent } from '../react-dom/diff'

const setStateQueue = []; // ‰øùÂ≠òÁªÑ‰ª∂stateChangeÁöÑÈòüÂàó
const renderQueue = []; // ‰øùÂ≠ò

// ÂºÇÊ≠•ÊâßË°å
function defer(fn) {
    return Promise.resolve().then(fn);
};

// Áî®‰∏Ä‰∏™ÈòüÂàóÊù•‰øùÂ≠òÊØèÊ¨°ÁöÑsetState
export function enqueueSetState(stateChange, component) {
    // Á¨¨‰∏ÄÊ¨°setStateÁöÑÊó∂ÂÄô
    if (setStateQueue.length === 0) {
        defer(flush);
    };
    // ÊääÁªÑ‰ª∂ÂíåÁªÑ‰ª∂Ëøô‰∏ÄÊ¨°ÁöÑsetStateÂÖ•ÈòüÂàó
    setStateQueue.push({
        stateChange,
        component
    });

    if (!renderQueue.some(item => item === component)) {
        renderQueue.push(component);
    };
};
// Ê∏ÖÁ©∫ÈòüÂàóÂπ∂Ê∏≤ÊüìÁªÑ‰ª∂
// stateChange1---component
// stateChange2---component
// stateChange3---component
// setStateÂá∫ÈòüÂàóÔºåcomponentÂá∫ÈòüÂàóÔºåÊõ¥Êñ∞component‰∏äÁöÑstate/Â¶ÇÊûúÊòØÂáΩÊï∞ÁöÑËØùÔºàËß£ÂÜ≥ÂºÇÊ≠•ÂØºËá¥viewÊõ¥Êñ∞‰∫ÜËßÜËßâ‰∏äÊï∞ÊçÆËøòÊ≤°Êõ¥Êñ∞ÔºâËøòÊõ¥Êñ∞setState

function flush() {
    let item, component;
    // ÈÅçÂéÜ‰øùÂ≠ò‰∫ÜÁªÑ‰ª∂setStateÁöÑÈòüÂàó
    while(item = setStateQueue.shift()) { // Âá∫ÈòüÂàó
        const { stateChange, component } = item; // ÁªÑ‰ª∂ÊîπÂèòÁöÑstateÂíåÁªÑ‰ª∂ÂÆû‰æã

        // Â¶ÇÊûúÊ≤°ÊúâprevStateÔºåÂàôÂ∞ÜÂΩìÂâçÁöÑstate‰Ωú‰∏∫ÂàùÂßãÁöÑprevState
        if (!component.prevState) {
            component.prevState = Object.assign({}, component.state);
        };

        // Â¶ÇÊûústateChangeÊòØ‰∏Ä‰∏™ÊñπÊ≥ïÔºå‰πüÂ∞±ÊòØsetStateÁöÑÁ¨¨‰∫åÁßçÂΩ¢ÂºèËß£ÂÜ≥Âõ†‰∏∫ÂºÇÊ≠•ÂØºËá¥ÁöÑËßÜËßâ‰∏äÊ≤°ÊúâÁõ¥Êé•Êõ¥Êñ∞Êï∞ÊçÆ
        if (typeof stateChange === 'function') {
            Object.assign(component.state, stateChange(component.prevState, component.props));
        } else {
            // Â¶ÇÊûústateChangeÊòØ‰∏Ä‰∏™ÂØπË±°ÔºåÂàôÁõ¥Êé•ÂêàÂπ∂Âà∞setState‰∏≠
            Object.assign(component.state, stateChange);
        };

        component.prevState = component.state;
    };

    // ÁªÑ‰ª∂ÈòüÂàó‰∏≠ÁöÑÁªÑ‰ª∂Âá∫ÈòüÂàó
    while(component = renderQueue.shift()) {
        renderComponent(component);
    };
};