export function setAttribute(dom, name, value) {
    // Â¶ÇÊûúÂ±ûÊÄßÂêçÊòØclassNameÔºåÂàôËøîÂõûclass
    if (name === 'className') {
        name = 'class';
    }
    // Â¶ÇÊûúÂ±ûÊÄßÂêçÊòØonXXXÔºåÂàôÊòØ‰∏Ä‰∏™ÁõëÂê¨ÊñπÊ≥ï
    if (/on\w+/.test(name)) {
        name = name.toLowerCase();
        dom[name] = value || '';
        // Â¶ÇÊûúÂ±ûÊÄßÂêçstyleÔºåÂàôÊõ¥Êñ∞styleÂØπË±°
    } else if (name === 'style') {
        if (!value || typeof value === 'string') {
            dom.style.cssText = value || '';
        } else if (value && typeof value === 'object') {
            for (let name in value) {
                // ÂèØ‰ª•ÈÄöËøástyle={ width: 20 }ËøôÁßçÂΩ¢ÂºèÊù•ËÆæÁΩÆÊ†∑ÂºèÔºåÂèØ‰ª•ÁúÅÁï•ÊéâÂçï‰Ωçpx
                dom.style[name] = typeof value[ name ] === 'number' ? value[ name ] + 'px' : value[ name ];
            }
        }
        // ÊôÆÈÄöÂ±ûÊÄßÂàôÁõ¥Êé•Êõ¥Êñ∞Â±ûÊÄß
    } else {
        if (name in dom) {
            dom[ name ] = value || '';
        }
        if (value) {
            dom.setAttribute(name, value);
        } else {
            dom.removeAttribute(name);
        }
    }
}